@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;1,300&display=swap');

.Weather-Wrapper {
    padding: 30px;
    border-radius: 10px;
    background: var(--color-modal);
    background-image: url(../../img/World2.webp);
    background-position: cover;
    background-size: cover;
    bottom: 10px;
    right: 10px;
    position: absolute;
    max-width: 350px;
    width: 100%;
    font-family: 'Montserrat', sans-serif;
    color: white;
    font-weight: bold;
    height: 400px;
    display: flex;
    align-items: center;
}
