.Weather-Wrapper.flex {
    display: flex;
    align-items: center;
    justify-content: center;
}

.city-name,
.city-temperature {
    font-size: 30px;
}

.weather-description {
    padding: 10px;
    font-size: 25px;
}

.weather-input {
    color: white;
    max-width: 350px;
    width: 100%;
    position: absolute;
    top: -50px;
    left: 50%;
    transform: translateX(-50%);
    background: var(--color-modal);
    outline: none;
    border: none;
    height: 40px;
    border-radius: 10px;
    padding-left: 10px;
    font-size: 20px;
    
}

.wrapper-weather-content {

    margin-top: 20px;
    line-height: normal;
    margin-bottom: 10px;
    /* border: 2px solid black; */
}

.weather-icons-wrapper {
    display: flex;
}

.weather-icon {
    max-width: 100px;
}

.MaxT-MinT {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-around;
    font-size: 24px;
    margin-left: 20px;
    /* border: 2px solid black; */

}

.refreshIcon {
    position: absolute;
    width: 50px;
    bottom: 20px;
    right: 20px;
}
.city-content-block{
    display: flex;
    /* border: 2px solid black; */
}
.city-content-block img{
    width: 30px;
    height: 30px;
    
}

.error {
    
    font-family: inherit;
}


/* Анимация */
.weather-enter {
    opacity: 0;
    /* transform: scale(0.1); */
    transform: translateY(100px);
}
.weather-enter-active {
    opacity: 1;
    transform: translateY(0);
    transition: opacity 300ms, transform 300ms;
}
.weather-exit {
    opacity: 1;
}
.weather-exit-active {
    opacity: 0;
    transform: translateY(100px);
    transition: opacity 300ms, transform 300ms;
}
@media screen and (max-width: 500px){
    .Weather-Wrapper{
        position: absolute;
        left: 50%;
        margin-right: -50%;
        transform: translateX(-50%);
    }
}