.runWindow {
    max-width: 500px;
    width: 100%;
    border-radius: 10px;
    position: absolute;
    height: 500px;
    left: .5%;
    background: rgba(38, 38, 50, 0.984);
    animation-duration: .5s;
    animation-name: openModal;
    transition: .2s;
    transform: translateX(-120%);
    top: -510px;
}
.runWindow.active{
    transform: translateX(0%);
}





