.panel_weather_wrapper{
    position: relative;
    width: 50px;
    margin-left: 1%;
}
.panel_weather_wrapper:hover{
    cursor: pointer;
    transform: scale(1.1);
}
.panel_weather_icon{
    width: 25px;
    height:25px;
}
.panel_weather_temp{
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--color-modal);
    border-radius: 5px;
    font-size: 12px;
    width: 24px;
    color: white;
    position: absolute;
    font-weight: normal;
    top: 0px;
    right: 10px;
}