.RunBtn{
    /* border: 2px solid green; */
    width: 23px;
    height: 23px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-left: 1%;
}
.RunBtn .elem-run-btn:nth-child(1){
    border-top-left-radius: 15%;
}
.RunBtn .elem-run-btn:nth-child(2){
    border-top-right-radius: 15%;
}
.RunBtn .elem-run-btn:nth-child(3){
    border-bottom-left-radius: 15%;
}
.RunBtn .elem-run-btn:nth-child(4){
    border-bottom-right-radius: 15%;
}

.RunBtn.active{
    outline-style: solid;
    outline-width: 7px;
    outline-color: rgba(0, 0, 0, .2);
    border-radius: 1%;
}