:root {
    --color-green: rgb(5, 172, 5);
    --color-lite-blue: #2196F3
}

* {

    font-weight: bold;
}

.ToDo-wrapper {
    /* display: none; */
    padding: 10px;
    max-width: 400px;
    width: 100%;
    min-height: 500px;
    max-height: 600px;
    background: white;
    position: absolute;
    right: 1%;
    border-radius: 10px;
    overflow: auto;
}

.ToDo-wrapper.active {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.ToDo-wrapper>button {
    font-weight: bold;
    border-radius: 5px;
    width: 50%;
    font-size: 12px;
    padding: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 10px;
    cursor: pointer;
    transition: .3s;
}

.ToDo-wrapper>button:hover {
    transform: scale(1.05);
}

.ToDo-wrapper input {
    display: block;
    width: 100%;
    margin: 10px auto;
    padding-left: 30px;
    height: 30px;
    border: none;
    border: 1px solid var(--color-lite-blue);
    border-radius: 4px;

}

.ToDo-wrapper input:focus {
    outline: none;
    border: none;
    border: 2px solid var(--color-green);
    border-radius: 4px;
}


.Header-ToDo {
    display: none;
    position: relative;
    /* border: 2px solid black; */
}

.Header-ToDo.active {
    width: 100%;
    display: flex;
    align-items: center;
}

.Header-ToDo button {
    transform: scale(.5);
    position: absolute;
    right: 0;

}


/* .content-wrapper button {
    height: 20px;
    font-size: 10px;
    padding: 5px;
    display: flex;
    align-items: center;
    margin-left: 0px;
} */
.content-wrapper {
    cursor: pointer;
    border-radius: 4px;
    color: white;
    background: var(--color-lite-blue);
    padding: 10px;
    max-width: 100%;
    width: 100%;
    display: flex;
    position: relative;
    /* border: 2px solid black; */
    align-items: center;
    justify-content: start;
    margin-top: 5px;
    margin-bottom: 5px;
    transition: .2s;
}

.content-wrapper.active {
    background: var(--color-green);
}

.content-wrapper:hover {
    transform: scale(1.01);
}

.content-wrapper .mui-btn--danger {
    border-radius: 5px;
    width: 15px;
    height: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    right: 20px;
    position: absolute;
}

.content-wrapper .mui-btn--danger:hover {
    transform: scale(1.1);
}

.ToDo-footer {
    width: 100%;
    display: none;
    /* border: 2px solid black; */
}

.ToDo-footer button {
    display: none;
}

.ToDo-footer button:nth-child(1) {
    font-weight: bold;
    text-transform: none;
    border-radius: 5px;
    width: 80px;
    height: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    font-size: 10px;
    animation-name: anime;
    animation-duration: 1.5s;
    animation-iteration-count: infinite;
    background: var(--color-green);
}

.ToDo-footer button:nth-child(1):hover {
    transform: scale(1.1);

}

@keyframes anime {
    0% {
        transform: scale(1);
    }

    50% {
        transform: scale(1.1)
    }

    100% {
        transform: scale(1);
    }
}

.ToDo-footer.active {
    display: flex;
    justify-content: center;
}

.wrapper-content__text {
    max-width: 100%;
    cursor: pointer;
    user-select: none;
    overflow: auto;
}

.wrapper-content__text.active {
    text-decoration: line-through;
}
.todo-completed-block{
    display: flex;
}

/* Css-transition */
.alert-enter {
    opacity: 0;
    transform: scale(0.1);
}

.alert-enter-active {
    opacity: 1;
    transform: translateX(0);
    transition: opacity 300ms, transform 300ms;
}

.alert-exit {
    opacity: 1;
}

.alert-exit-active {
    opacity: 0;
    transform: scale(0.1);
    transition: opacity 300ms, transform 300ms;
}

