
#SearchInput{
    display: block;
    height: 30px;
    width: 90%;
    margin: 0 auto;
    margin-top: 30px;
    border-radius: 5px;
    background: rgb(49, 49, 65);
    border: none;
    border: 2px solid rgba(15, 14, 14, 0.5);
    border-bottom: 2px solid var(--green-color);
}
#SearchInput:focus{
    background-color: rgb(25, 25, 33);
    border: none;
    outline: none;
    
    border: 2px solid rgba(15, 14, 14, 0.5);
    border-bottom: 2px solid var(--green-color);
}
#SearchInput::placeholder{
    color:rgb(130, 130, 152);
    padding-left: 30px;
    margin-left: 30px;

}
